import React, { Fragment } from "react";
import { useSelector} from "react-redux";
import {Link} from 'react-router-dom';
import {LeftOutlined} from "@ant-design/icons"


const Scheduled=()=> {

    const list=useSelector((state)=>state.todoReducer.list);

    const showDates=()=>{
        const today= new Date().toLocaleDateString();

        return(
        list.map((elem,index)=>{
            const date=elem.data.timestamp;
            if(new Date(today)>new Date(date)){
            return(
                <Fragment>
                    <span className="checked-item">
                        <span className="item">{elem.data.todo_title}</span>
                    </span>
                    <div className="date">
                        {elem.data.timestamp}
                    </div>
                    <hr></hr>
            </Fragment>   
            )}
            else{ 
            return(
            <div key={index}>                 
            <span className="check">
                <span className="item">{elem.data.todo_title}</span>
                <div className="date">
                    {elem.data.timestamp}
                </div><hr></hr>
                </span>
                <hr></hr>
            </div>
        )}
    })
        )
  }
  return ( 
    <div>
        <div className="add-reminder-btn">
            <Link to="/"><h3><LeftOutlined/>All todos:</h3></Link>
        </div>
        <div className="show">
            {showDates()}
        </div>
    </div>
 );
}
  
  
  
  export default Scheduled;